import { _decorator, CCBoolean, Component, Label, Node, PlaceMethod, Sprite, VideoPlayer, sys, director, SceneAsset } from 'cc';
import { move } from './move';
import { limites } from './limites';
const { ccclass, property } = _decorator;

@ccclass('ManagerGame')
export class ManagerGame extends Component {
    @property(CCBoolean)
    public gameover : boolean = false;
    @property(CCBoolean)
    public Pase : boolean = false;
    @property(Node)
    public gameOver : Node = null;
    @property(Label)
    public Scorefinal : Label = null;
    @property(Node)
    public buttonexit : Node = null;
    @property(Label)
    public scoregame:Label = null;
    @property(Node)
    public player : Node = null;
    @property(Node)
    public vidas : Node = null;
    @property(CCBoolean)
    public PC : boolean = false;
    @property(Node)
    public mobile : Node = null;
    private coche : Node;

    
    onLoad() {
        for(let i = 0 ; i< this.player.children.length; i++)
            {
                 if(this.player.children[i].active)
                    {
                        this.coche = this.player.children[i];
                    }
            }
        this.gameOver.active =false;
        this.vidas.active=true;
        // Verifica si el juego se está ejecutando en un dispositivo móvil
        if (sys.isMobile) {

            var progress = 0;
            director.preloadScene(this.mobile.toString(), (completedCount: number, totalCount: number, item: any) => {
            progress = completedCount / totalCount;
         }, (error: Error, asset: SceneAsset) => {
                //do something after preloaded scene
                director.loadScene(this.mobile.toString());
            });
            // Código específico para dispositivos móviles
          // this.PC.active=false;
           this.mobile.active=true;
           //this.listaFinal[0]=this.mobile;
        } else {
            // Código para otras plataformas (por ejemplo, navegadores)
            this.PC =true;
            this.mobile.active=false;
           

           //this.listaFinal[0]=this.PC;
            /* var progress = 0;
            director.preloadScene(this.PC.toString(), (completedCount: number, totalCount: number, item: any) => {
             progress = completedCount / totalCount;
           }, (error: Error, asset: SceneAsset) => {
             //do something after preloaded scene
            //director.loadScene(this.PC.toString());
            });*/
        }
    }

    start() {
        

    }
    
    bajavida(){
        if(this.coche.getComponent(limites).vidas==6){
            this.vidas.children[2].active=false;
        }
        if(this.coche.getComponent(limites).vidas==3){
            this.vidas.children[1].active=false;

        }
        if(this.coche.getComponent(limites).vidas==0){
            this.vidas.children[0].active=false;

        }
    }
    PaseTag(){

        if(this.Pase)
            {
                this.coche.getComponent(move).enabled = false;
                this.coche.getComponent(move).PasaNivel();
                this.scheduleOnce(function(){
                   this.coche.getComponent(move).enabled = true;
                   this.Pase=false;


                },1)
                

            }

    }
    
    GameOver(){

        this.gameOver.active=true;
        this.Scorefinal.string = this.scoregame.string;
        
    }

    update(deltaTime: number) {
        
    }
}


