import { _decorator, Component, Node, Touch, Vec3, UITransform, EventTouch, Vec2 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('palanca')
export class palanca extends Component {
    @property(Node)
    public palanca : Node = null;
    @property(Node)
    public player : Node = null;
    @property
    public maxr : number = 100;
    @property(Vec2)
    public dir:Vec2 = new Vec2;

    onLoad(){
        this.palanca.setPosition(new Vec3(0,0,0))
        this.node.on(Node.EventType.TOUCH_MOVE, this.on_stick_move, this)
        this.node.on(Node.EventType.TOUCH_END, this.on_stick_end, this)
        this.node.on(Node.EventType.TOUCH_CANCEL, this.on_stick_end,this)

    }

    //on stick move
    private on_stick_move(event:Touch){

        const screepos= event.getUILocation()
        const posui= new Vec3(screepos.x,screepos.y,0)
        const pos= this.node.getComponent(UITransform).convertToNodeSpaceAR(posui)
        const len = pos.length()
        if(len<=0){
            this.palanca.setPosition(pos);
            return
        }
        this.dir.x=pos.x/len;
        this.dir.y = pos.y/len;
        if(len> this.maxr){
            pos.x= pos.x*this.maxr/len;
            pos.y = pos.y * this.maxr/len;

        }
       this.palanca.setPosition(pos)

    }

    // on stick move end or cancel

    private on_stick_end(event:Touch){
        this.dir.x=0;
        this.dir.y=0;
        this.palanca.setPosition(new Vec3(0,0,0))
    }
    start() {

    }

    update(deltaTime: number) {
        
    }
}


