import { _decorator, Component, Node, Vec3 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('FollowPlayer')
export class FollowPlayer extends Component {

    @property(Node)
    public player : Node;

    @property public coche : Node;

    onLoad() {
        for(let i = 0 ; i< this.player.children.length; i++)
            {
                 if(this.player.children[i].active)
                    {
                        this.coche = this.player.children[i];
                    }
            }
        
    }
    start() {

    }

    update(deltaTime: number) {
        this.node.position= new Vec3(this.coche.position.x,this.coche.position.y, 1)
    }
}


