import { _decorator, Component, Node, Collider, ICollisionEvent, SpriteRenderer, CCInteger, find  } from 'cc';
import { move } from './move';
import { ManagerGame } from './ManagerGame';
import { Moneda } from './Moneda';
import { Manager } from './Manager';
const { ccclass, property } = _decorator;

@ccclass('limites')
export class limites extends Component {
    @property(ManagerGame)
    public manager : ManagerGame = null;
    @property(CCInteger)
    public score : number =0;
    @property(CCInteger)
    public vidas : number = 10;

    @property(Node) man:Node=null;
    
    onLoad() {
        
    }
    start() {
        
        let collider = this.node.getComponent(Collider);
    // Listening to 'onCollisionStay' Events
    collider.on('onCollisionEnter', this.onCollisionEnter, this);
    collider.on("onTriggerEnter",this.onTriggerEnter, this);
    }

    onCollisionEnter(event: ICollisionEvent){
      
       if(event.otherCollider.getGroup()== 4){
        console.log("choaste");
        this.vidas --;
        this.manager.bajavida();
        if(this.vidas <= 0)
            {
                this.manager.GameOver();
            }
        //restar vidas
       }

       if(event.otherCollider.getGroup()==16){
        console.log("tocaste caseta tienes tarjeta??");
            this.manager.PaseTag();
        }
       
    }

    onTriggerEnter(event: ICollisionEvent){

      let moneda = event.otherCollider;
       if(moneda.getGroup()== 32){
        moneda.node.getComponent(Collider).enabled=false;
       moneda.getComponent(SpriteRenderer).enabled=false;
       event.otherCollider.node.destroy();
        this.score+= moneda.node.parent.getComponent(Moneda).valor;

       
        this.man.getComponent(Manager).ActualizaScore(true);
        console.log("moneda");
        
       }

       if(event.otherCollider.getGroup()==64){
            event.otherCollider.getComponent(SpriteRenderer).enabled=false;
            event.otherCollider.getComponent(Collider).enabled=false;
           
            event.otherCollider.node.destroy();
            this.score+= event.otherCollider.getComponent(Moneda).valor;
            this.manager.scoregame.string= this.score.toString();
            this.manager.Pase=true;
         }
     
    }
    
    

    update(deltaTime: number) {
        
    }
}


