import { _decorator, CCFloat, Component, Node, Quat, randomRangeInt, RigidBody, SpriteFrame, SpriteRenderer, KeyCode, v4, Vec3, find } from 'cc';
import { palanca } from './palanca';
import { InputManager } from './InputManager';
import { tarjetasspawn } from './tarjetasspawn';
import { Moneda } from './Moneda';
import { ManagerGame } from './ManagerGame';


const { ccclass, property } = _decorator;

@ccclass('move')
export class move extends Component {

    @property(palanca)
    public stick : palanca = null;

    @property(InputManager)
    public inputManager: InputManager =null;
    @property(Vec3)
    public moveDirection : Vec3 = new Vec3;
    @property (CCFloat)
    public angle : number = 0;
    @property(Vec3)
    public targetPosition : Vec3 = new Vec3;
    @property
    public speed : number = 0;
    @property(Vec3)
    public newPosition : Vec3 = new Vec3;
    @property(Node)
    public nodeImage : Node = null;
    public rigid : RigidBody = null;
    public sprite : SpriteRenderer = null;
    @property([SpriteFrame])
    public listaimagenes : SpriteFrame[] = [];
    @property([Node])
    public Niveles : Node [] = [];
    @property([Node])
    public TarjetasManager : Node []= [];
    @property([Node])
    public MonedasManager : Node [] = [];

    @property ([Node])
    public inicios : Node [] = [];

    private inicioMapa : number = 0;

    @property(Node)
    public gamemager :  Node = null;


    onLoad(){
        
        this.gamemager= find("ManagerGame")
        this.inicioMapa = 0;
        
        this.node.getParent().setParent(this.Niveles[this.inicioMapa])
       this.nodeImage.position= this.inicios[this.inicioMapa].position;
       this.Niveles[this.inicioMapa].active=true;
       this.TarjetasManager[this.inicioMapa].getComponent(tarjetasspawn).spawn();
     // this.spawnmonedas();
        this.rigid= this.nodeImage.getComponent(RigidBody);
        this.sprite = this.nodeImage.getComponent(SpriteRenderer)
    }
    start() {

    }
    spawnmonedas(){
        for(var i = 0; i < this.MonedasManager[this.inicioMapa].children.length; i++ ){
           // console.log("entre" + i);
            //this.MonedasManager[this.inicioMapa].children[i].getComponent(Moneda).aparece();
    
           }
    }
    mueve(deltaTime){
        console.log("mueve con teclas")
        if(!InputManager.isKeyPressed(KeyCode.ARROW_RIGHT)&&!InputManager.isKeyPressed(KeyCode.ARROW_LEFT) && !InputManager.isKeyPressed(KeyCode.ARROW_DOWN) && !InputManager.isKeyPressed(KeyCode.ARROW_UP))
            {
                this.rigid.clearVelocity();
                return;
    
            }
        
        if(InputManager.isKeyPressed(KeyCode.ARROW_LEFT))
        {
         this.moveDirection.x -= 1 /10     
         
        }
        if(InputManager.isKeyPressed(KeyCode.ARROW_RIGHT)){
        this.moveDirection.x += 1/10
        }
        if (InputManager.isKeyPressed(KeyCode.ARROW_UP)){
            this.moveDirection.y+=1/10
        }
        if (InputManager.isKeyPressed(KeyCode.ARROW_DOWN)){
            this.moveDirection.y-=1/10
        }

        

///*
//OPTIM
        this.angle = Math.atan2(this.moveDirection.x, this.moveDirection.y) * (-180 / Math.PI);
        
        
        Vec3.normalize(this.moveDirection, this.moveDirection);


        this.nodeImage.angle=this.angle;
       
        
        this.rigid.setLinearVelocity(new Vec3(this.moveDirection.x * this.speed,this.moveDirection.y * this.speed,0));
    
   
}
    PasaNivel(){
        this.inicioMapa +=1;
        if(this.inicioMapa >= this.Niveles.length)
            {
                this.inicioMapa =0;
            }
        this.node.getParent().setParent(this.Niveles[this.inicioMapa])
        this.TarjetasManager[this.inicioMapa].getComponent(tarjetasspawn).spawn();
        //this.spawnmonedas();
        if(this.inicioMapa == 0){
            this.Niveles[3].active=false;
        }
        else{
            this.Niveles[this.inicioMapa-1].active=false;
        }
       

     this.nodeImage.position = this.inicios[this.inicioMapa].position;
     this.Niveles[this.inicioMapa].active=true;
    }

    mueveStick()
    {
        if(this.stick.dir.x==0 && this.stick.dir.y==0){
            this.rigid.clearVelocity();
            this.angle=0;
            return;
        }
        this.moveDirection.x+=this.stick.dir.x;
        this.moveDirection.y+=this.stick.dir.y;
        this.angle = Math.atan2(this.moveDirection.x, this.moveDirection.y) * (-180 / Math.PI);
        
        
        Vec3.normalize(this.moveDirection, this.moveDirection);
      
        
        this.nodeImage.angle=this.angle;

          /* if(this.moveDirection.y>=.95){
                //console.log("arriba")
                this.sprite.spriteFrame = this.listaimagenes[0];
              


            }
            if(this.moveDirection.y<=-.95){
               // console.log("abajo")
                
              
                this.sprite.spriteFrame = this.listaimagenes[1];

            }
            if(this.moveDirection.x>=.95)
                {
                  //  console.log("derecha")
                    this.sprite.spriteFrame = this.listaimagenes[2];
                   // console.log(this.nodeImage.getWorldRotation());
                   
                   // console.log(this.nodeImage.getWorldRotation());
                }
                if(this.moveDirection.x<=-.95){
                   // console.log("Izquierda")
                    this.sprite.spriteFrame = this.listaimagenes[3];
                   
    
                }*/
        this.rigid.setLinearVelocity(new Vec3(this.moveDirection.x * this.speed,this.moveDirection.y * this.speed,0));
    }
    update(deltaTime: number) {
        this.mueve(deltaTime);
       
    }
}


